.burger-menu-nav-item-home {
  margin-bottom: 15px;
}

.burger-menu-signup-btn {
  min-width: 184px;
  cursor: pointer;
  background: #4f2ee8;
  border: 1.5px solid #111;
  border-radius: 18px;
  justify-content: space-between;
  align-items: center;
  // margin-top: 20px;
  margin-left: 20px;
  padding: 13px 14px;
  display: flex; //flex!!!!!!!!!!!!//
  margin-bottom: 130px;
}

.burger-menu-signup-btn-text {
  letter-spacing: -0.04em;
  color: #fff;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28571;
}

.burger-menu-container {
  width: 375px;
  height: 812px;
  z-index: 1;
  // opacity: 0;
  // visibility: hidden;
  // pointer-events: none;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  background: linear-gradient(#4f2ee8 0%, #fff 130%);
  position: fixed;
  top: 0;
  left: 0;
  position: relative;
  padding-top: 40px;
  // transform: translateY(-100%);
}

.burger-menu-nav-backgimage-container {
  background-image: url(/src/images/burger-menu-image.png);
  background-position: bottom 248px right 0px;
  background-repeat: no-repeat;
  padding: 0;
  background-size: contain;
  width: 100%;
  height: 100%;
}

