.burger-menu-nav {
  display: flex;
  justify-content: center;
  margin-top: 126px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

//код для зміненого стилю  кнопки home і shopping list///
.burger-menu-nav-item-changed {
  background-color: #eac645;
  border-radius: 36px;
  padding: 8px 16px;
  margin-bottom: 15px;
}

//код для зміненого стилю кнопки home і shopping list//
.burger-menu-nav-link-changed {
  letter-spacing: -0.04em;
  text-transform: uppercase;
  color: #111;
  align-items: center;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28;
  display: flex;
}

.burger-menu-nav-item {
  padding: 8px 16px;
}

.burger-menu-nav-link-sl {
  letter-spacing: -0.04em;
  text-transform: uppercase;
  color: #ffffff;
  align-items: center;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28;
  display: flex;
}

.burger-menu-nav-svg-sl {
  fill: #ffffff;
  margin-left: 4px;
}

.burger-menu-btn-logout {
  cursor: pointer;
  background-color: #fff;
  border: 1.5px solid #111;
  border-radius: 18px;
  justify-content: space-between;
  align-items: center;
  margin-left: 20px;
  padding: 14px 24px;
  display: flex;
  position: absolute;
  bottom: 20px;
}

.burger-menu-btn-logout-text {
  font-family: inherit;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.28;
  letter-spacing: -0.04em;

  color: #111111;
}

.burger-menu-btn-logout-svg {
  margin-left: 4px;
}
